@mixin stick {
    width: 100%;
    height: 2px;
    border-radius: 2px;
    position: absolute;
    left: 0;
}

.menu-button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    cursor: pointer;
    position: relative;
    
    .stick-top {
        @include stick;
        transition: transform .4s, top .4s .4s;
        top: 0;
    }

    .stick-mid {
        @include stick;
        top: calc(50% - 1px);
    }

    .stick-bot {
        @include stick;
        transition: transform .4s, bottom .4s .4s;
        bottom: 0;
    }

    &.open {
        .stick-top {
            top: calc(50% - 1px);
            transition: top .4s, transform .4s .4s;
            transform: rotate(45deg);
        }
    
        .stick-mid {
            display: none;
        }
    
        .stick-bot {
            bottom: calc(50% - 1px);
            transition: bottom .4s, transform .4s .4s;
            transform: rotate(-45deg);
        }
    }
}