.fullscreen-image {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    min-height: 100vh;
    background-color: rgba(0,0,0,0.7);
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    overflow: hidden;

    img {
        max-width: 100%;
    }

    .thumbnails {
        position: absolute;
        bottom: 50px;
        transition: transform .8s;

        .thumbnail-img {
            display: inline-block;
            width: 50px;
            height: 40px;
            padding: 4px;
            cursor: pointer;

            &:hover {
                transform: translateY(-5px);
            }
        }
    }

    .change-button {
        position: absolute;
        top: calc(50% - 48px);
        font-size: 96px;
        color: rgba(0,0,0,0.5);
        cursor: pointer;

        &:hover  {
            color: $main-green;
        }

        &.left-button {
            left: 20px;
        }

        &.right-button {
            right: 20px;
        }
    }
}

@media(max-width: 600px) {
    .fullscreen-image {
        .change-button {
            font-size: 48px;
    
            &.left-button {
                left: 10px;
            }
    
            &.right-button {
                right: 10px;
            }
        }
    }
}