#price {
    min-height: 100vh;
    padding: 20px 0px;
    display: flex;

    .loading {
        height: auto;
    }

    .price-content {
        width: 100%;
        
        ul {
            li {
                p {
                    text-align: initial;
                }
            }
        }
    
        & > ul {
            min-height: calc(100vh - 118px);
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            align-items: flex-start;
            padding: 0 15px;
    
            & > li {
                padding: 10px 10px 10px 30px;
                background-color: black;
                color: $main-green;
                border-radius: 16px;
                display: block;
                box-shadow: 0 0 8px $extra-gray;
                min-width: 50%;
                transition: transform .2s;

                @media (max-width: 500px) {
                    & {
                        margin: 2px 0;
                    }
                }
    
                &:hover {
                    transform: translateY(5px);
                }
            }
    
            @media (min-width: 500px) {
                & > li:nth-child(even) {
                    align-self: flex-end;
                }
            }
        }
    }
}