.introduction {
    min-height: 100vh;
    background-color: black;
    display: flex;
    flex-direction: column;
    padding-top: 120px;
    padding-bottom: 20px;
    color: $main-white;

    @media (max-height: 800px) {
        & {
            padding-top: 80px;
        }
    }

    .brand {
        z-index: 1;
    }

    .more-info {
        display: flex;
        .intro {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            padding: 0 15px;
        }
    
        .illustrate {
            display: flex;
            align-items: center;
            justify-content: center;
    
            img {
                width: 50%;
                z-index: 0;
            }
        }
    
        @media (max-width: 780px) {
            .illustrate {
                display: none;
            }
    
            .half {
                width: 100%;
            }
        }
    }
}