.scroll-top-button {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    width: 24pt;
    height: 24pt;
    border-radius: 50%;
    position: fixed;
    right: calc(50% - 12pt);
    bottom: 18pt;
    box-shadow: 0 0 16px black;

    &:hover {
        background-color: $main-green;
        transition: background-color .4s;
    }
}