@keyframes slide-down {
    from {
        position: relative;
        bottom: 15px;
    }
    to {
        position: relative;
        bottom: 0;
    }
}

@keyframes slide-up {
    from {
        position: relative;
        top: 15px;
    }
    to {
        position: relative;
        top: 0;
    }
}

@keyframes appear {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.gallery-show {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 10px;
    justify-items: center;
    align-items: center;
    animation: slide-down .4s ease;

    @media (max-width: 900px) {
        & {
            grid-template-columns: 1fr 1fr;
        }    
    }

    @media (max-width: 600px) {
        & {
            grid-template-columns: 1fr;
        }    
    }

    .img-holder {
        position: relative;
        height: 300px;
        box-shadow: 3px 3px 30px 3px rgba(0,0,0,.2);
        cursor: pointer;
        overflow: hidden;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        .img-mask {
            display: none;
        }

        &:hover {
            transform: translateY(4px);

            .img-mask {
                color: $main-white;
                width: 100%;
                height: 100%;
                position: absolute;
                background-color: rgba(0,0,0,0.6);
                display: flex;
                flex-direction: column;
                justify-content: center;

                .img-name {
                    animation: slide-down .4s ease;
                }

                .img-desc {
                    animation: slide-up .4s ease;
                }

                .img-price {
                    animation: appear .8s ease;
                }
            }
        }
    }
}