#product {
    padding-top: 20px;
    padding-bottom: 20px;
    display: flex;

    .loading {
        height: auto;
    }

    .product-gallery {
        min-height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;

        .gallery-show {
            margin: 20px 0;
            flex: 1;
        }
    }

    @media (max-width: 696px) {
        & {
            .paginate {
                font-size: 25px;
                .page {
                    height: 35px;
                    width: 35px;
                    margin: 0 10px;
                }
            }
        }
    }

    @media (max-width: 560px) {
        & {
            .paginate {
                font-size: 18px;
                .page {
                    height: 30px;
                    width: 30px;
                    margin: 0 2px;
                }
            }
        }
    }
}