// WEIGHT
.text-bold {
    font-weight: bold;
}

// SIZE
.text-large {
    font-size: 102px;
}

.text-quite-large {
    font-size: 92px;
}

.text-medium {
    font-size: 48px;
}

.text-quite-medium {
    font-size: 27px;
}

.text-quite-small {
    font-size: 16px;
}

.text-small {
    font-size: 12px;
}

// COLOR
.text-green {
    color: $main-green;
}

// RELATION
.text-nowrap {
    white-space: nowrap;
}