@keyframes appear {
    from {opacity: 0;}
    to {opacity: 1;}
}

.layout {
    .drowned-background {
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        z-index: -1;
        opacity: .5;
    }

    .scroll-top-button {
        animation: appear .8s;
    }    
}