tton{right:10px}}@-webkit-keyframes slide-down{from{position:relative;bottom:15px}to{position:relative;bottom:0}}@keyframes slide-down{from{position:relative;bottom:15px}to{position:relative;bottom:0}}@-webkit-keyframes slide-up{from{position:relative;top:15px}to{position:relative;top:0}}@keyframes slide-up{from{position:relative;top:15px}to{position:relative;top:0}}@-webkit-keyframes appear{from{opacity:0}to{opacity:1}}@keyframes appear{from{opacity:0}to{opacity:1}}.gallery-show{width:100%;display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:10px;justify-items:center;align-items:center;-webkit-animation:slide-down .4s ease;animation:slide-down .4s ease}@media (max-width: 900px){.gallery-show{grid-template-columns:1fr 1fr}}@media (max-width: 600px){.gallery-show{grid-template-columns:1fr}}.gallery-show .img-holder{position:relative;height:300px;box-shadow:3px 3px 30px 3px rgba(0,0,0,0.2);cursor:pointer;overflow:hidden}.gallery-show .img-holder img{width:100%;height:100%;object-fit:cover}.gallery-show .img-holder .img-mask{display:none}.gallery-show .img-holder:hover{-webkit-transform:translateY(4px);transform:translateY(4px)}.gallery-show .img-holder:hover .img-mask{color:#fff;width:100%;height:100%;position:absolute;background-color:rgba(0,0,0,0.6);display:flex;flex-direction:column;justify-content:center}.gallery-show .img-holder:hover .img-mask .img-name{-webkit-animation:slide-down .4s ease;animation:slide-down .4s ease}.gallery-show .img-holder:hover .img-mask .img-desc{-webkit-animation:slide-up .4s ease;animation:slide-up .4s ease}.gallery-show .img-holder:hover .img-mask .img-price{-webkit-animation:appear .8s ease;animation:appear .8s ease}.text-bold{font-weight:bold}.text-large{font-size:102px}.text-quite-large{font-size:92px}.text-medium{font-size:48px}.text-quite-medium{font-size:27px}.text-quite-small{font-size:16px}.text-small{font-size:12px}.text-green{color:#03fc0b}.text-nowrap{white-space:nowrap}@keyframes appear{from{opacity:0}to{opacity:1}}.lay