.footer {
    width: 100%;
    min-height: 300px;
    background-color: black;
    color: $main-white;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;

    .contact {
        font-size: 20px;
        display: flex;
        flex-direction: column;
        justify-content: center;

        .info {
            display: flex;
            margin: 5px;

            .about {
                padding-right: 30px;
            }
        }
    }

    .map {
        margin: 15px 0;

        .map-embeded {
            width: 600px;
            height: 300px;
        }
    }

    @media (max-width: 1487px) {
        & {
            justify-content: space-evenly;
        }
    }

    @media (max-width: 660px) {
        .map {
            .map-embeded {
                width: 500px;
                height: 250px;
            }
        }
    }

    @media (max-width: 540px) {
        .map {
            .map-embeded {
                width: 400px;
                height: 200px;
            }
        }
    }

    @media (max-width: 450px) {
        .map {
            .map-embeded {
                width: 350px;
                height: 200px;
            }
        }
    }
}