.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 120px;
    color: $main-white;
    background-color: inherit;
    font-weight: bold;
    position: fixed;
    top: 0;
    z-index: 99;

    a {
        color: $main-white;
    }

    .main-logo {
        font-size: 24px;
    }

    .main-nothing {
        width: 110px;
        display: flex;
        justify-content: flex-end;
    }

    .main-navigation {
        display: flex;

        .menu-item {
            margin: 0 16px;
        }        
    }

    &.collapse {
        .main-navigation {
            display: block;
            position: absolute;
            top: 80%;
            left: 0;
            right: 0;
            padding: 15px 0;
            background-color: $extra-black;
            font-weight: normal;
            font-size: 14px;
            text-align: start;
            overflow: hidden;
            max-height: 350px;
            transition: padding .8s, max-height .8s;

            a {
                color: $main-white;
            }

            .menu-item {
                display: block;
                padding: 20px 0;
                margin: 0 40px;
            }

            .menu-item:not(:last-child) {
                border-bottom: 1px solid $main-gray;
            }
        }

        .menu-hide {
            max-height: 0;
            padding: 0;
        }
    }

    &.white-header {
        background-color: $main-white;
        height: 80px;

        a {
            color: black;
        }

        .main-navigation {
            top: 100%;
        }

        .main-nothing {
            .menu-button {
                .stick {
                    background-color: black !important;
                }
            }
        }
    }    
}