.paginate {
    .page {
        display: inline-block;
        width: 40px;
        height: 40px;
        cursor: pointer;
        margin: 0 20px;

        &:before {
            content: '';
            display: inline-block;
            height: 100%;
            vertical-align: middle;
        }

        &:hover {
            color: $main-green;
        }

        &.chosen {
            background-color: $main-green;
            border-radius: 50%;

            &:hover {
                color: inherit;
                cursor: not-allowed;
            }
        }
    }
}